@use 'base/variables' as v;
@use 'base/mixins' as m;

main{
    background-color: v.$softRed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2.4rem;
    border-radius: 2rem;

    img{
        height: 4.5rem;
    }
}

.balance{
    display: block;
    text-align: start;
    &__title{
        font-size: 1.8rem;
        color: v.$paleOrange;
        font-family: v.$dmSans;
        font-weight: 400;
        margin: .5rem 0;
        @include m.mobile {
            font-size: 1.6rem;
        }
    }
    &__amount{
        font-size: 3rem;
        color: v.$paleOrange;
        font-family: v.$dmSans;
        font-weight: 700;
    }
}