@use 'base/variables' as v;
@use 'base/mixins' as m;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after{
    box-sizing: inherit;
}
body{
    font-size: 1.8rem;
    background-color: v.$cream;
    padding-top: 8rem;
    @include m.mobile {
        padding-top: 18rem;
    }
}
.contenedor{
    width: 95%;
    max-width: 180rem;
    @include m.mobile {
        max-width: 45rem;
    }
    margin: 0 auto;
}
h1,h2,h3,p{
    margin: 0;
}
a{
    text-decoration: none;
}
