@use 'base/variables' as v;
@use 'base/mixins' as m;

.spending{
    background-color: v.$paleOrange;
    padding: 2rem;
    margin-top: 2rem;
    border-radius: 2rem;
    @include m.mobile {
        padding-top: 3rem;
        padding-bottom: 4rem;
    }

    &__title{
        color: v.$darkBrown;
        font-family: v.$dmSans;
        font-weight: 700;
        text-align: left;
        @include m.mobile {
            padding-bottom: 4rem;
        }
    }

    .divider{
            height: 2px;
            margin: 3rem auto;
            background-color: v.$cream;
            border-radius: 2rem;
    }

    &__summary{
        display: flex;
        justify-content: space-between;
        font-family: v.$dmSans;

        .summary{
            display: block;
            &__title{
                color: v.$mediumBrown;
                font-weight: 400;
                font-size: 1.8rem;
                text-align: left;
                @include m.mobile {
                    font-size: 1.6rem;
                    padding-bottom: .25rem;
                }
            }

            &__amount{
                color: v.$darkBrown;
                font-size: 3.5rem;
                font-weight: 700;
                text-align: center;
                @include m.mobile {
                    font-size: 4.2rem;
                }
            }
        }

        .profit{
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            text-align: right;
            &__percentage{
                color: v.$darkBrown;
                font-weight: 700;
                @include m.mobile {
                    padding-bottom: .1rem;
                }
            }
            &__description{
                color: v.$mediumBrown;
                font-weight: 400;
            }
        }
    }
}
// Chart
.spending__chart{
    max-height: 100%;
}