/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.8rem;
  background-color: hsl(27deg, 66%, 92%);
  padding-top: 8rem;
}
@media (min-width: 375px) {
  body {
    padding-top: 18rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 180rem;
  margin: 0 auto;
}
@media (min-width: 375px) {
  .contenedor {
    max-width: 45rem;
  }
}

h1, h2, h3, p {
  margin: 0;
}

a {
  text-decoration: none;
}

