main {
  background-color: rgb(236, 119, 95);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.4rem;
  border-radius: 2rem;
}
main img {
  height: 4.5rem;
}

.balance {
  display: block;
  text-align: start;
}
.balance__title {
  font-size: 1.8rem;
  color: hsl(33deg, 100%, 98%);
  font-family: "DM Sans", sans-serif;
  font-weight: 400;
  margin: 0.5rem 0;
}
@media (min-width: 375px) {
  .balance__title {
    font-size: 1.6rem;
  }
}
.balance__amount {
  font-size: 3rem;
  color: hsl(33deg, 100%, 98%);
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
}

.spending {
  background-color: hsl(33deg, 100%, 98%);
  padding: 2rem;
  margin-top: 2rem;
  border-radius: 2rem;
}
@media (min-width: 375px) {
  .spending {
    padding-top: 3rem;
    padding-bottom: 4rem;
  }
}
.spending__title {
  color: hsl(25deg, 47%, 15%);
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  text-align: left;
}
@media (min-width: 375px) {
  .spending__title {
    padding-bottom: 4rem;
  }
}
.spending .divider {
  height: 2px;
  margin: 3rem auto;
  background-color: hsl(27deg, 66%, 92%);
  border-radius: 2rem;
}
.spending__summary {
  display: flex;
  justify-content: space-between;
  font-family: "DM Sans", sans-serif;
}
.spending__summary .summary {
  display: block;
}
.spending__summary .summary__title {
  color: hsl(28deg, 10%, 53%);
  font-weight: 400;
  font-size: 1.8rem;
  text-align: left;
}
@media (min-width: 375px) {
  .spending__summary .summary__title {
    font-size: 1.6rem;
    padding-bottom: 0.25rem;
  }
}
.spending__summary .summary__amount {
  color: hsl(25deg, 47%, 15%);
  font-size: 3.5rem;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 375px) {
  .spending__summary .summary__amount {
    font-size: 4.2rem;
  }
}
.spending__summary .profit {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: right;
}
.spending__summary .profit__percentage {
  color: hsl(25deg, 47%, 15%);
  font-weight: 700;
}
@media (min-width: 375px) {
  .spending__summary .profit__percentage {
    padding-bottom: 0.1rem;
  }
}
.spending__summary .profit__description {
  color: hsl(28deg, 10%, 53%);
  font-weight: 400;
}

.spending__chart {
  max-height: 100%;
}

.footer {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  font-family: "DM Sans", sans-serif;
  text-align: center;
}
.footer a {
  color: rgb(236, 119, 95);
}